<template>
	<div id="store-category">
		<public-header :title="store.title"></public-header>
		<div class="container" :class="{'has-child-4': categorySelected && categorySelected.child && categorySelected.child.length > 0 && categorySelected.child.length <= 4, 'has-child-8': categorySelected && categorySelected.child && categorySelected.child.length > 4}">
			<div class="category-container bg-default">
				<van-swipe
					:width="90"
					:height="97"
					:show-indicators="false"
					:loop="false"
				>
					<van-swipe-item
						class="cate-item"
						:class="{active: item.id == categorySelectedId}"
						v-for="(item, index) in categoryAll" :key="item.id"
						@click="onToggleCategory(item.id, index, '')"
					>
						<div class="img-wrap">
							<img class="img-100" :src="item.thumb" alt="">
						</div>
						<div class="cate-title ellipsis">{{item.title}}</div>
					</van-swipe-item>
				</van-swipe>
				<div class="category-children van-hairline--top" v-if="categorySelected && categorySelected.child && categorySelected.child.length > 0">
					<div class="cate-child-item "
						v-for="(item, index) in categorySelected.child"
						:key="item.id"
						@click="onToggleCategory(item.id, index, 'child')"
					>
						<div class="child-inner ellipsis" :class="{active: item.id == childSelectedId}">{{item.title}}</div>
					</div>
				</div>
			</div>
			<van-list
				class="goods-container"
				v-model="goodsItem.loading"
				:finished="goodsItem.finished"
				@load="onGetGoods"
				:offset="50"
				:immediate-check="false"
			>
				<template v-if="store.goods_style == 1">
					<!--单列-->
					<div class="diy-goods-single-row">
						<div class="goods-item" v-for="item in goodsItem.data" :key="item.id">
							<router-link class="goods-img" tag="div" :to="util.getUrl({path: '/pages/store/goodsDetail', query: {id: item.id, sid: store.id}})">
								<img v-lazy="{src: item.thumb, loading: lazyload_goods}">
								<div class="discount hide" v-if="item.unitnum > 1">{{item.unitnum_multi_cn}}购</div>

							</router-link>
							<router-link class="goods-info" tag="div" :to="util.getUrl({path: '/pages/store/goodsDetail', query: {id: item.id, sid: store.id}})">
								<div>
									<div class="goods-name">{{item.title}}</div>
									<div class="sale-num">已售:{{item.sailed}} 好评率:{{item.comment_good_percent}}</div>
									<div class="svip-price-tips" v-if="item.svip_status == 1 && !item.discount_price">
										<div class="svip-price">
											<i class="icon icon-crownfill"></i>
											<span>{{item.discount}}折</span>
										</div>
										<div class="svip-activity">超级会员专享</div>
										<div class='discount margin-10-l' v-if="item.unitnum > 1">
											<van-icon name='tag'></van-icon>{{item.unitnum_multi_cn}}购
										</div>
									</div>
									<div class='discount'  v-else>
										<template v-if="item.discount">
											<van-icon name='tag'></van-icon>
											{{item.discount}}折 限{{item.max_buy_limit}}份 {{item.unitnum > 1 ? item.unitnum_multi_cn + '购' : ''}}
										</template>
										<template v-else-if="item.unitnum > 1">
											<van-icon name='tag'></van-icon>{{item.unitnum_multi_cn}}购
										</template>
									</div>
								</div>
								<div>
									<div class="goods-price">
										<div class="in-b now-price">￥{{item.price}}<span class="font-14">{{item.unitname_cn}}</span></div>
										<div class="in-b old-price" v-if="item.old_price > 0">￥{{item.old_price}}</div>
									</div>
								</div>
							</router-link>
							<goods-handle
								:goods="item"
								:optionId="0"
								from="list"
								@onParentPlus="onParentPlus"
								@onParentSelectOption="onParentSelectOption"
								@onParentPopupNotInSailTime="onParentPopupNotInSailTime"
								@showMoveDot="showMoveDotFun"
							>
							</goods-handle>
						</div>
					</div>
				</template>
				<template v-else-if="store.goods_style == 2">
					<!--双列-->
					<div class="diy-goods-double-row">
						<div class="goods-item" v-for="item in goodsItem.data" :key="item.id">
							<div class="goods-item-inner">
								<router-link tag="div" :to="util.getUrl({path: '/pages/store/goodsDetail', query: {id: item.id, sid: store.id}})" class="goods-img">
									<img v-lazy="{src: item.thumb, loading: lazyload_goods}">
									<div class="discount" v-if="(item.svip_status != 1 || item.discount_price > 0) && item.discount > 0">
										<div>
											<van-icon name="tag"></van-icon> {{item.discount}}折 限{{item.max_buy_limit}}份 {{item.unitnum > 1 ? item.unitnum_multi_cn + '' : ''}}
										</div>
									</div>
									<div class="discount" v-if="item.unitnum > 1 && !item.discount_price">
										<div>
											<van-icon name="tag"></van-icon> {{item.unitnum_multi_cn}}
										</div>
									</div>
								</router-link>
								<router-link tag="div" :to="util.getUrl({path: '/pages/store/goodsDetail', query: {id: item.id, sid: store.id}})" class="goods-info">
									<div class="info-top">
										<div class="goods-name">{{item.title}}</div>
										<div class="svip-price-tips" v-if="item.svip_status == 1 && !item.discount_price">
											<div class="svip-price">
												<span class="ellipsis">{{item.discount}}折</span>
											</div>
											<div class="svip-activity ellipsis">超级会员专享</div>
										</div>
										<div class="sale-num" v-else>已售:{{item.sailed}} 好评率:{{item.comment_good_percent}}</div>
									</div>
									<div class="goods-price">
										<div class="now-price">￥{{item.price}}<span class="font-14">{{item.unitname_cn}}</span></div>
										<div class="old-price margin-5-t" v-if="item.old_price > 0">￥{{item.old_price}}</div>
									</div>
								</router-link>
								<goods-handle
									:goods="item"
									:optionId="0"
									from="list"
									@onParentPlus="onParentPlus"
									@onParentSelectOption="onParentSelectOption"
									@onParentPopupNotInSailTime="onParentPopupNotInSailTime"
									@showMoveDot="showMoveDotFun"
								>
								</goods-handle>
							</div>
						</div>
					</div>
				</template>
				<load v-if="goodsItem.empty == 1" type="loaded" text="暂无商品" bgcolor="#f5f5f5"></load>
				<load v-else-if="goodsItem.finished" type="loaded" text="已经到底了" bgcolor="#f5f5f5"></load>
			</van-list>
		</div>
		<store-cart
			show="1"
			:store="store"
			:cart="cart"
			:pindan_id="0"
			@selectPinadan="selectPinadan"
		>
		</store-cart>
		<!--可售时间弹出层-->
		<van-popup v-model="popupNotInSailTime" class="not-in-time-popup" v-if="popupNotInSailTime">
			<div class='popup-top'>
				<div class='popup-item' v-if="goodsActive.week_cn">
					<div class='popup-title'>每周可售日期</div>
					<div class='popup-text'>{{goodsActive.week_cn}}</div>
				</div>
				<div class='popup-item' v-if="goodsActive.time_cn">
					<div class='popup-title'>每天可售时间</div>
					<div class='popup-text'>
						{{goodsActive.time_cn}}
					</div>
				</div>
			</div>
			<div class='popup-bottom border-1px-t' @click="popupNotInSailTime = false">知道了</div>
		</van-popup>
		<!--规格弹出层-->
		<van-popup v-model="ParentPopupSpecShow" class="options-popup" v-if="ParentPopupSpecShow">
			<div class="options-dialog">
				<div class="muti-close" @click="ParentPopupSpecShow = false"></div>
				<div class="muti-wrap">
					<div class="muti-food-title border-1px-b">
						{{goodsActive.title}}
					</div>
					<div class="muti-cont">
						<div class='muti-cont-inner'>
							<div class='muti-sec first' v-if="goodsActive.is_options == 1">
								<div class='muti-sec-title'>规格</div>
								<div class='muti-choice'>
									<template v-for="(item, index) in goodsActive.options">
										<div :class="{selected: (goodsActive.activeOptions.optionSelected == item.id)}" @click="onParentToggleOption(item.id, '', 'option')">{{item.name}}</div>
									</template>
								</div>
							</div>
							<div class='muti-sec' v-if="goodsActive.is_attrs == 1">
								<template v-for="(attrItem, attrIndex) in goodsActive.attrs">
									<div class='muti-sec-title'>{{attrItem.name}}</div>
									<div class='muti-choice'>
										<template v-for="(label, labelIndex) in attrItem.label">
											<div :class="{selected: (goodsActive.activeOptions.attrsSelected[attrIndex] == labelIndex)}" @click="onParentToggleOption(attrIndex, labelIndex, 'attr')">{{label}}</div>
										</template>
									</div>
								</template>
							</div>
						</div>
					</div>
					<div class="muti-choose">
						<div class='muti-oprt'>
							<div class='muti-cart-oprt clearfix'>
								<div class='add-food'>
									<van-icon name='plus' @click="onParentPlus($event, goodsActive,  '', '', '1')" style="background-color: #6BBA50;"></van-icon>
								</div>
								<template v-if="goodsSpecNum">
									<div class='food-num'>{{goodsSpecNum}}</div>
									<div class='remove-food'>
										<div class='remove icon icon-minus' @click="onParentMinus(goodsActive)"></div>
									</div>
								</template>
							</div>
						</div>
						<div class='muti-info'>
							<div class='muti-price'>¥{{goodsActive.activeOption.price}}</div>
							<div class='muti-type'>({{goodsActive.activeOption.name}})</div>
						</div>
					</div>
				</div>
			</div>
		</van-popup>
		<!--小球-->
		<transition
				appear
				@after-appear = 'onParentAfterEnter'
				@before-appear="onParentBeforeEnter"
				v-for="(item,index) in showMoveDot"
				:key="index"
		>
			<div class="parent_move_dot" v-if="item"></div>
		</transition>
		<!--普通用户购买超级会员商品弹出层-->
		<van-popup v-model="popupBuySvip" class="buy-svip">
			<div class='popup-svip-title'>客官，专享价限超级会员享受哦</div>
			<div class="svip-img">
				<div class="svip-vs-left">
					<i class="icon icon-crownfill"></i>
					<div class="font-12">会员专享价</div>
					<div class="svip-vs-price"><span class="font-14">¥</span>{{goodsActive.svip_price}}</div>
				</div>
				<div class="svip-vs-right">
					<div class="svip-putong-title">普通用户价格</div>
					<div class="svip-putong-price"><span class="font-14">¥</span>{{goodsActive.origin_price}}</div>
				</div>
			</div>
			<div class="buy-svip-text">即刻加入超级会员，尊享众多会员优惠</div>
			<button class="svip-price-btn font-16 margin-15-t">
				<div class="font-bold" @click="onParentPlus($event, goodsActive, '', '', '3')">会员专享价购买</div>
				<div class="font-12 padding-5-t">(订单结算时成为会员享优惠)</div>
			</button>
			<van-button size="normal" class="price-btn font-16 margin-15-t" @click="onParentPlus($event, goodsActive, '', '', '2')">原价购买</van-button>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
	import PublicHeader from '@/components/header'
	import StoreCart from '@/components/storeCart'
	import GoodsHandle from '@/components/goodsHandle'
	import Load from '@/components/load'
	export default {
		components: {
			PublicHeader,
			StoreCart,
			GoodsHandle,
			Load
		},
		data() {
			return {
				sid: 0,
				cid: 0,
				goodsActive: {},
				goodsAll: [],
				goodsItem: {
					page: 2,
					psize: 30,
					loading: false,
					finished: false,
					empty: 0,
					data: [],
				},
				categorySelectedIndex: 0,
				categorySelectedId: 0,
				categoryAll:[],
				categorySelected: {
					child: [{}]
				},
				childSelectedIndex: 0,
				childSelectedId: 0,

				store: {},
				cart: {},
				lazyload_goods: '',
				showMoveDot: [],
				popupBuySvip: false,
				popupNotInSailTime: false,
				ParentPopupSpecShow: false,
				windowHeight: 0,

				showPreLoading: true
			}
		},
		methods: {
			...mapMutations([
				'replaceStore', 'replaceCart', 'replaceState'
			]),
			onLoad() {
				var that = this;
				that.util.request({
					url: 'wmall/store/category',
					data: {
						sid: that.sid,
						cid: that.cid
					}
				}).then((res) => {
					that.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return false;
					}
					result = result.message;
					that.lazyload_goods = result.config_mall.lazyload_goods;
					that.store = that.util.extend({}, result.store);
					that.cart = that.util.extend({}, result.cart);
					that.replaceStore(result.store);
					that.replaceCart(result.cart.message.cart);
					that.util.setWXTitle(that.store.title)

					that.goodsItem.loading = false;
					that.goodsItem.data = [...result.goods];
					if(that.goodsItem.data.length < that.goodsItem.psize) {
						that.goodsItem.finished = true;
						if(!that.goodsItem.data.length) {
							that.goodsItem.empty = 1;
						}
					}

					that.categoryAll = [...result.category];
					that.categorySelectedId = result.cid;
					that.childSelectedId = result.child_id;
					that.categorySelectedIndex = result.cindex;
					that.goodsAll[that.categorySelectedIndex] = [];
					that.goodsAll[that.categorySelectedIndex][that.childSelectedIndex] = that.goodsItem;
					that.categorySelected = result.category[that.categorySelectedIndex] || {};
				})
			},

			selectPinadan(pindan_id) {
				if(pindan_id > 0) {
					var url = this.util.getUrl({path: '/pages/store/goods', query: {sid: this.sid, pindan_id: pindan_id}});
					this.$router.replace(url);
				}
			},

			onToggleCategory(cid, index, from) {
				var that = this;
				var childIndex = 0;
				if(from == 'child') {
					childIndex = index;
					that.childSelectedIndex = childIndex;
					that.categoryAll[that.categorySelectedIndex]['childSelectedIndex'] = childIndex;
					that.categorySelected = that.categoryAll[that.categorySelectedIndex];
				} else {
					if(that.categoryAll[index]['childSelectedIndex'] > 0) {
						childIndex = that.categoryAll[index].childSelectedIndex;
					}
					that.categorySelected = that.categoryAll[index];
					that.categorySelectedIndex = index;
					that.childSelectedIndex = childIndex;
					that.categoryAll[index]['childSelectedIndex'] = childIndex;
				}
				that.categorySelectedId = that.categorySelected.id;
				that.childSelectedId = 0;
				if(that.categorySelected.child && that.categorySelected.child.length > 0) {
					that.childSelectedId = that.categorySelected.child[childIndex]['id'];
				}
				that.onGetGoods();
			},

			onParentPlus: function(event, goods, optionId, from, buysvip_status) {
				this.goodsActive = goods;
				var buysvip = 0;
				if(this.icart.pindan_id == 0) {
					if(this.icart.is_buysvip == 1) {
						buysvip = 1;
					} else {
						if(this.icart.svip_buy_show >= 1 && goods.svip_buy_show == 1) {
							if(buysvip_status == 1) {
								if(this.goodsActive.activeOption) {
									if(this.goodsActive.activeOption.origin_price > 0) {
										this.goodsActive.origin_price = this.goodsActive.activeOption.origin_price;
									} else {
										this.goodsActive.origin_price = this.goodsActive.activeOption.price;
									}
									if(this.goodsActive.activeOption.svip_price > 0) {
										this.goodsActive.svip_price = this.goodsActive.activeOption.svip_price;
									} else {
										this.goodsActive.svip_price = this.goodsActive.activeOption.price;
									}
								}
								this.popupBuySvip = true;
								return;
							} else if(buysvip_status == 2) {
								this.popupBuySvip = false;
							} else if(buysvip_status == 3) {
								this.popupBuySvip = false;
								buysvip = 1;
							}
						}
					}
				}
				var goods_id = goods.id || goods.goods_id;
				var option_id = goods.activeOptionId || optionId;
				var params = {
					sid: this.istore.id,
					goods_id: goods_id,
					option_id: option_id,
					num: 1,
					sign: '+',
					is_buysvip: buysvip
				};
				this.util.request({
					url: 'wmall/store/goods/cart',
					//showLoading: true,
					data: params,
				}).then((result) => {
					var result = result.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return false;
					}
					if(result.message.msg) {
						this.$toast(result.message.msg);
					}

					// 加入购物车动画
					let elLeft = event.target.getBoundingClientRect().left;
					let elBottom = event.target.getBoundingClientRect().bottom;
					if(from == 'cart'){
						elBottom = elBottom + 50;
					}
					this.elLeft = elLeft;
					this.elBottom = elBottom;
					this.showMoveDot.push(true);
					this.replaceCart(result.message.cart);
				})
			},

			onParentMinus: function(goods, optionId, from) {
				var buysvip = 0;
				if(this.icart.is_buysvip == 1) {
					buysvip = 1;
				}
				var goods_id = goods.id || goods.goods_id;
				var option_id = goods.activeOptionId || optionId;
				var params = {
					sid: this.istore.id,
					goods_id: goods_id,
					option_id: option_id,
					num: 1,
					sign: '-',
					is_buysvip: buysvip
				};
				this.util.request({
					url: 'wmall/store/goods/cart',
					//showLoading: true,
					data: params,
				}).then((result) => {
					var result = result.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return false;
					}
					this.replaceCart(result.message.cart);
				})
			},

			onParentSelectOption: function(goods) {
				this.goodsActive = goods;
				this.goods = goods;
				this.ParentPopupSpecShow = true;
				if(!this.goods['activeOptions']) {
					this.onParentGoodsOptionInit();
				}
			},

			onParentGoodsOptionInit: function() {
				this.goodsActive = this.goods;
				this.goodsActive['activeOptions'] = {
					option: 0,
					attrs: [],
					optionSelected: 0,
					attrsSelected: [],
					num: 0
				};
				if(this.goodsActive.is_options == 1) {
					this.goodsActive['activeOptions']['option'] = this.goodsActive.options[0]['id'];
					this.goodsActive['activeOptions']['optionSelected'] = this.goodsActive.options[0]['id'];
				}
				if(this.goodsActive.is_attrs == 1) {
					for(var i = 0; i < this.goodsActive.attrs.length; i++) {
						this.goodsActive['activeOptions']['attrs'].push(i + "s0");
						this.goodsActive['activeOptions']['attrsSelected'][i] = 0;
					}
				}
				this.onParentToggleActiveOption();
			},

			onParentToggleOption: function (optionId, optionValueID, type) {
				var goodsActive = this.goods;
				if(type == 'option') {
					goodsActive['activeOptions']['option'] = optionId;
					goodsActive['activeOptions']['optionSelected'] = optionId;
				} else {
					goodsActive['activeOptions']['attrs'][optionId] = optionId + 's' + optionValueID;
					goodsActive['activeOptions']['attrsSelected'][optionId] = optionValueID;
				}
				this.goodsActive = goodsActive;
				this.onParentToggleActiveOption();
			},

			onParentToggleActiveOption: function(option_key) {
				var goodsActive = this.goodsActive;
				if(!option_key) {
					goodsActive['activeOptionId'] = goodsActive['activeOptions']['option'];
					if(goodsActive['activeOptions']['attrs'].length > 0) {
						goodsActive['activeOptionId'] = goodsActive['activeOptionId'] + '_' + goodsActive['activeOptions']['attrs'].join('v')
					}
				} else {
					goodsActive['activeOptionId'] = option_key;
				}
				goodsActive['activeOption'] = goodsActive['options_data'][goodsActive['activeOptionId']];
				goodsActive['activeOption']['num'] = this.parentGetSpecNum();
				this.goodsActive = Object.assign({}, goodsActive)
			},

			onParentPopupNotInSailTime: function(goods){
				this.goodsActive = goods;
				this.popupNotInSailTime = true;
			},

			parentGetSpecNum: function(){
				if(!this.icart|| !this.icart['data1'] || !this.icart['data1'][this.goodsActive.id]) {
					return 0;
				}
				var optionId = this.goodsActive.activeOptionId;
				if(!this.icart['data1'][this.goodsActive.id][optionId]) {
					return 0;
				}
				return this.icart['data1'][this.goodsActive.id][optionId]['num'];
			},

			onGetGoods() {
				var that = this;
				if(!that.goodsAll[that.categorySelectedIndex]) {
					that.goodsAll[that.categorySelectedIndex] = [];
				}
				that.goodsItem = that.goodsAll[that.categorySelectedIndex][that.childSelectedIndex];
				if(!that.goodsItem) {
					that.goodsItem = {
						page: 1,
						psize: 30,
						loading: true,
						finished: false,
						empty: 0,
						data: []
					};
				} else {
					if(that.goodsItem.empty) {
						return false;
					}
					if(that.goodsItem.finished) {
						console.log('商品全部加载完成');
						return false;
					}
				}
				that.goodsItem.loading = true;
				that.util.request({
					url: 'wmall/store/goods/goods',
					data: {
						sid: that.sid,
						page: that.goodsItem.page,
						psize: that.goodsItem.psize,
						cid: that.categorySelectedId,
						child_id: that.childSelectedId,
					},
				}).then((res) => {
					that.goodsItem.loading = false;
					var result = res.data.message.message;
					that.goodsItem.data = [...that.goodsItem.data, ...result.goods];
					that.goodsItem.page++;
					if(result.goods.length < that.goodsItem.psize) {
						that.goodsItem.finished = true;
						if(!result.goods.length) {
							that.goodsItem.empty = 1;
						}
					}
					if(!that.goodsAll[that.categorySelectedIndex]) {
						that.goodsAll[that.categorySelectedIndex] = [];
					}
					that.goodsAll[that.categorySelectedIndex][that.childSelectedIndex] = that.goodsItem;
				})
			},

			showMoveDotFun: function(showMoveDot, elLeft, elBottom) {
				this.showMoveDot.push(true);
				this.elLeft = elLeft;
				this.elBottom = elBottom;
			},

			onParentBeforeEnter(el){
				el.style.bottom = this.windowHeight - this.elBottom + 'px';
				el.style.left = this.elLeft +5 + 'px';
				el.style.opacity = 0;
			},

			onParentAfterEnter(el){
				el.style.bottom = -20 + 'px';
				el.style.left = 20 + 'px';
				el.style.opacity = 1;
				setTimeout(() => {
					this.replaceState({key: 'cart_animate', val: true});
				}, 400)
			},


		},
		computed: {
			...mapState([
				'istore', 'icart',
			]),

			goodsSpecNum: function(){
				if(!this.icart|| !this.icart['data1'] || !this.icart['data1'][this.goodsActive.id]) {
					return 0;
				}
				var optionId = this.goodsActive.activeOptionId;
				if(!this.icart['data1'][this.goodsActive.id][optionId]) {
					return 0;
				}
				return this.icart['data1'][this.goodsActive.id][optionId]['num'];
			},

		},
		mounted() {
			var query = this.util.parseQuery(this.$route.query);
			if(query.sid > 0) {
				this.sid = query.sid;
			}
			if(query.cid > 0) {
				this.cid = query.cid;
			}
			this.onLoad();
			setTimeout(()=> {
				this.windowHeight = window.innerHeight;
			},50)
		},
		watch: {
			'$route' (to, from) {

			}
		}
	}
</script>

